.card-container{
    display: flex;
    flex-wrap: wrap;
    margin-left:20rem;
    margin-top: 2rem;
    z-index: -2;
}


.card{
    margin: 20px;
    border: 2px solid #c0c0c0;
    padding: 20px;
    border-radius: 15px;
    cursor: pointer;
    box-shadow: 1px 4px 2px 0px rgba(0,0,0,0.6);
}

.card-img{
    width: 13rem;
    margin-bottom: 1rem;
    
}

.card-title{
    margin-bottom: 1rem;
    word-wrap: wrap;
}

.card-reviews{
    margin-bottom: 1rem;
    display: flex;
}

.card-price{
     display: flex;
     justify-content: space-around;
     align-items: center;
     font-weight: bolder;
}

@media screen and (max-width: 768px) and (min-width: 375px) {
    .card-container{
        display: flex;
        margin-left:10rem;
        margin-top: 2rem;
        z-index: -2;
    }
    .card-img{
        width: 20rem;
        margin-bottom: 1rem;
        
    }
    .card{
        margin: 20px;
        border: 2px solid #c0c0c0;
        padding: 20px;
        border-radius: 15px;
        cursor: pointer;
        box-shadow: 1px 4px 2px 0px rgba(0,0,0,0.6);
    }
  }
  body{
    background-color: rgb(255, 255, 255);
  }